.product-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2.5rem;

    @media screen and (min-width: 576px) and (max-width: 992px) {
        grid-template-columns: repeat(2, 1fr);
      }
    
      @media screen and (max-width: 576px) {
        grid-template-columns: repeat(1, 1fr);
    }
}